<script>
import axios from 'axios'
import store from './store/data.js'

// 导入模拟数据
import video from './util/video.js';
import conference from './util/conference.js';
import course from './util/course.js';
import member from './util/member.js';

export default {
  name: 'app',

  data () {
    return {
     conference:[],
     courses:[],
     members:[],
     videos:[],
     isShowNav: true,
     isShowVideo: false,
     videoUrl:'',
     scrollTop0: true
    }
  },

  created(){
    
       
  },
  methods:{

    // 注册点击事件
    
    // 搜索按钮
    registerSearchEvent(){
      let searchBtn = this.$refs.searchBtn; 
      let inputBtn = this.$refs.inputBtn;  
      searchBtn.addEventListener("click", ()=>{
          
          if( inputBtn.value && !this.isShowNav ){

            // 发起请求 并跳转页面
            alert("发起请求");

          } else {
           
            // 把导航栏取消
            this.isShowNav = !this.isShowNav;
          } 

      });
    },

    //离焦请求
    // inputBlur(){
    //   let inputBtn = this.$refs.inputBtn;      
    //   inputBtn.addEventListener("blur",()=>{
    //      this.isShowNav = !this.isShowNav;
    //   })
    // },

    // 弹出模态框
    displayVideo(url){
      this.isShowVideo = true;
      this.videoUrl = url;
    },
    // 关闭模态框
    toclose(){
      this.isShowVideo = false;
      // 清空video的src属性
      this.videoUrl="";
    },

    // 窗口滚动事件
    navScroll(){

      window.addEventListener("scroll",()=>{

        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (!scrollTop) {
            this.scrollTop0 = true;
        } else {
           this.scrollTop0 = false;
        }
      })

    }
  },
  computed: {

    filterConf(){
      return this.conference.slice(1,7);
    },
    ConfNum1(){
      return this.conference.slice(0,1);
    }


    
  },
  mounted () {

    // 数据请求
    axios.get("api/video").then(

          (res) => { 
            this.videos = res.data;
            //console.log(this.videos);
          },

    );

    axios.get("api/conference").then(

          (res) => { 
            this.conference = res.data;
            //console.log(this.conference);
          },

    );

    axios.get("api/course").then(

          (res) => { 
            this.courses = res.data;
            //console.log(this.conference);
          },

    );

    axios.get("api/member").then(

          (res) => { 
            this.members = res.data;
            //console.log(this.conference);
          },

    );


    // 相关事件的开启

    // 搜索按钮事件
    this.registerSearchEvent();
    //this.inputBlur();
    this.navScroll();

   
  }
}
</script>

